body {
  font-family: sans-serif;
}

button {
  margin: 0 auto;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

input, button {
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 30px;
}

input[type="submit"], button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(45, 45, 233, .73);
  border: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.photo-card {
  width: 350px;
  text-align: center;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

.gallery-img {
  width: 350px;
  height: 250px;
}

.info {
  display: flex;
}

.gallery__item {
  color: #000;
  text-decoration: none;
}

.load-more {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

